.our-principles {
  min-height: 100vh;
  background-color: #edb733;
  padding-top: 100px;
  padding-bottom: 100px;

  &__list {
    margin: 0;
    margin-top: 100px;

    list-style: none;
    padding: 0;
    counter-reset: principles-list;
    
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 100px;
  }

  &__item {
    position: relative;
    font-size: 24px;
    line-height: 1.2;
    text-transform: uppercase;
    padding: 2px;
    padding-right: 20px;
    border-radius: 8px;

    min-width: 200px;
    box-shadow: 5px 2px 3px 1px rgba(white, 50%);

    &::before {
      position: absolute;
      top: -200%;
      right: 0;
      counter-increment: principles-list;
      content: counter(principles-list) " ";
      color: rgba(white, 50%);
      font-size: 60px;
      line-height: 1.5;
    }
  }
  
}