.intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;

  background-color: #191919;
  background-image: url("../img/content/intro-4.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;

  &__subtitle {
    font-size: 36px;
    line-height: 1.2;
    margin: 0;
    color: $color-default-white;
    text-align: center;
    text-transform: uppercase;
  }

  &__cloud-mask {
    width: 100%;
    position: absolute;
    bottom: -8px;
    left: 0;
    right: 0;

    & svg {
      width: 100%;
    }
  }
}
