.delivery {
  background-color: #edb733;
  padding-top: 100px;
  padding-bottom: 100px;
  min-height: 100vh;

  &__subtitle {
    font-size: 20px;
    list-style: 1.3;
    text-align: center;
    margin-bottom: 40px;
  }

  &__list {
    display: flex;
    gap: 20px;
    justify-content: center;
    list-style: none;
    margin: 0;
    padding: 0;
    margin-bottom: 50px;
  }

  &__item {
    display: flex;
    width: 30%;
    border-radius: 20px;
    transition: box-shadow $trans-default, transform $trans-default;

    &:hover {
      box-shadow: 1px 3px 20px 5px rgba(white, 80%);
      transform: translateY(-10px);
    }
  }

  &__item-wrap {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: #191919;
    border: 2px solid transparent;
    color: white;
    border-radius: 20px;
    padding: 30px;
    box-shadow:  0px 10px 15px -3px rgba(0, 0, 0, 0.3);

    &--popular {
      border-color:#bedbff;
    }
  }

  &__label {
    display: block;
    position: absolute;
    background-color: #155dfc;
    padding: 3px 30px;
    border-radius: 30px;
    font-size: 14px;
    line-height: 1.2;
    top: -12px;
    left: 50%;
    transform: translateX(-50%);
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 85px;
    height: 85px;
    border-radius: 50px;
    border: 3px solid #ffd500;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;

    & svg {
      width: 40px;
      height: 40px;
    }

    &--self {
      // background-color: #f8fafc;

      & svg {
        // color: #191919;
      }
    }
  }

  &__status {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600px;
    padding: 16px;
    background-color: rgba(#ffd500, 10%);
    border-radius: 12px;
    text-align: center;
    min-height: 80px;
  }

  &__text {

  }

  &__point {
    font-size: 14px;
    color: rgba(white, 70%);
  }

  &__detail-info {
    display: flex;
    align-items: center;
    background-color: rgba(#f9fafb, 40%);
    padding: 12px;
    border-radius: 12px;
    color: #191919;
    margin-top: auto;

    & svg {
      width: 24px;
      height: 24px;
      margin-right: 20px;
    }
  }

  &__additional-info {
    text-align: center;
    margin-bottom: 50px;
    padding: 30px;
    background-color: wheat;
    border-radius: 20px;
  }

  &__additional-title {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;

    & svg {
      width: 24px;
      height: 24px;
      margin-right: 20px;
    }
  }

  &__btn {
    margin-left: 50%;
    transform: translate(-50%);
  }
}