.faq {
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: #191919;
  background-image: url("../img/sprite/seat-texture.svg");
  color: white;

  &--padding {
    padding: 40px 0;
  }

  &__wrapper {}

  &__title {}

  &__accordion-element {
    &.is-active {
      & .faq__accordion-btn {
        & svg {
          transform: rotate(90deg);
        }
      }

      & .faq__accordion-content--border {
        border-bottom-color: $color-link-water;
      }

      & .faq__accordion-btn--border {
        border-bottom-color: transparent;
      }
    }
  }

  &__accordion-btn {
    display: flex;
    align-items: center;
    color: $color-default-white;
    background-color: #191919;
    border: 1px solid transparent;
    font-weight: 600;
    font-size: 18px;
    line-height: 1.2;
    transition: color $trans-default, background-color $trans-default, border-color $trans-default;

    &--border {
      border-bottom: 1px solid $color-link-water;
    }

    &:hover,
    &:focus {
      color: #edb733;
      background-color: #292929;
      border-bottom-color: #edb733;

      & svg {
        fill: #edb733;
      }
    }

    &:active {
      color: rgba(#edb733, 70%);
      background-color: #292929;
      border-bottom-color: #edb733;

      & svg {
        fill: #edb733;
      }
    }

    & svg {
      width: 32px;
      height: 32px;
      margin-right: 5px;
      transition: transform $trans-default;
      flex-shrink: 0;
      fill: white;
      transition: fill $trans-default, transform $trans-default;
    }

    @include vp-767 {
      text-align: left;
    }
  }

  &__accordion-content {
    color: #191919;
    background-color: white;
    border-radius: 12px;

    &--border {
      border: 1px solid transparent;
    }
  }
}