.advantages {
  position: relative;
  padding-top: 100px;
  padding-bottom: 150px;
  background-color: #191919;


  background: radial-gradient(circle at center,
      #f5f5f5,
      #191919);

  background: radial-gradient(circle at center,
      #292929 0%,
      #191919 70%);

  /* Размеры и центрирование контента */
  // min-height: 100vh;
  // display: flex;
  // // flex-direction: column;
  // align-items: center;
  // justify-content: center;
  // padding: 2rem;


  &__box {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
  }

  &__item {
    max-width: 300px;
    position: relative;

    &::before {
      width: 2px;
      height: 150px;
      background-color: #edb733;
      border-radius: 12px;
    }

    &-1,
    &-2,
    &-3,
    &-4 {
      grid-column: 1 / span 4;
      text-align: right;
    }

    &-2 {
      grid-row: 2;
    }

    &-3 {
      grid-row: 3;
    }

    &-4 {
      grid-row: 4;
    }

    &-5 {
      grid-row: 4;
      grid-column: 5 / span 4;
      justify-self: center;
    }

    &-8,
    &-9,
    &-10,
    &-11,
    &-12 {
      grid-row: 1;
      grid-column: 10 / span 3;
      justify-self: end;
    }

    &-9 {
      grid-row: 2;
    }

    &-10 {
      grid-row: 3;
    }

    &-11 {
      grid-row: 4;
    }

    &-12 {
      grid-row: 5;
    }

    &-1 {
      &::before {
        content: "";
        position: absolute;
        top: -16px;
        right: -158px;
        transform: rotate(-50deg);
      }
    }

    &-4 {
      &::before {
        content: "";
        position: absolute;
        top: -112px;
        right: -158px;
        transform: rotate(50deg);
      }
    }

    &-5 {
      & .advantages__item-title {
        &::before {
          top: -100%;
          left: 50%;
        }

        &::after {
          top: -200%;
          left: 43.3%;
          transform: rotate(90deg);
        }
      }

      & .advantages__item-title,
      & .advantages__item-text  {
        text-align: center;
      }
    }

    &-8,
    &-9,
    &-10,
    &-11 {
      & .advantages__item-title {

        &::before {
          left: -50px;
          top: 50%;
          transform: translateY(-50%);
        }

        &::after {
          top: 50%;
          transform: translateY(-50%);
          left: -100px;
          width: 50px;
          height: 2px;
          background-color: #edb733;
        }
      }
    }

    &-8 {
      &::before {
        content: "";
        position: absolute;
        top: -16px;
        left: -158px;
        transform: rotate(50deg);
      }
    }

    &-11 {
      &::before {
        content: "";
        position: absolute;
        top: -112px;  
        left: -158px;
        transform: rotate(-50deg);
      }
    }
  }

  &__item-title {
    position: relative;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.2;
    color: #edb733;
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 10px;

    &::before {
      content: "";
      position: absolute;
      right: -50px;
      top: 50%;
      transform: translateY(-50%);
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background-color: #edb733;
    }

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: -100px;
      width: 50px;
      height: 2px;
      background-color: #edb733;
    }
  }

  &__item-text {
    font-weight: 500;
    color: white;
    line-height: 1.5;
    margin: 0;
  }

  &__picture {
    height: 600px;
    grid-row: 1 / span 3;
    grid-column: 5 / span 4;
    justify-self: center;

    & img {
      height: 100%;
    }
  }




}