.callback {
  min-height: 100vh;
  background-color: #edb733;
  padding-top: 100px;
  padding-bottom: 100px;

  &__form {
  }

  &__row {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    & label {
      display: flex;
      flex-wrap: wrap;
      font-weight: 500;
      font-size: 18px;
      line-height: 1.2;
    }
      
    & input,
    & textarea {
      width: 100%;
      font-size: 20px;
      line-height: 1.2;
      margin-top: 10px;
      padding: 10px 20px;
      border: 2px solid #191919;
      border-radius: 12px;
      transition: box-shadow $trans-default;

      &:hover,
      &:focus {
        box-shadow: 1px 3px 20px 5px rgba(white, 80%);
      }
    }
  }
}