.our-works {
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: #edb733;
  // background-image: url("../img/svg/point-pattern.svg");

  &__slider {
    margin-bottom: 50px;
    padding: 30px;
  }

  &__slide {
    height: 100%;
  }

  &__label {
    display: inline-flex;
    font-weight: 500;
    font-size: 18px;
    background-color: #f5f5f5;
    color: #191919;
    margin: 0;
    margin-bottom: 20px;
    justify-content: flex-end;
    padding: 10px 20px;
    border-top-left-radius: 12px;
    border-bottom-right-radius: 12px;
  }

  &__img {
    border: 10px solid #f5f5f5;
    background-color: #f5f5f5;
    border-radius: 12px;
    transition: transform $trans-default;

    &:hover {
      transform: scale(1.03);
    }

    & img {
      // height: 100%;
      width: 100%;
      height: 500px;
      object-fit: cover;
      border-radius: 12px;
    }
  }

  & .swiper {
    & .our-works__pagination {
      bottom: -5px;
    }
  }

  &__description {
    background-color: wheat;
    margin: 0;
    margin-bottom: 30px;
    padding: 30px;
    border-radius: 20px;
  }
  
  &__subtitle {
    font-weight: 500;
    margin: 0;
  }

  &__important {
    display: flex;
    justify-content: flex-end;
    
    & span {
      background-color: #f5f5f5;
      margin: 0;
      padding: 10px 20px;
      border-radius: 12px;
    }
  }
}