.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  color: #edb733;
  background-color: transparent;
  border: 2px solid #edb733;
  border-radius: 12px;
  font-weight: 900;
  cursor: pointer;

  text-transform: uppercase;
  transition: opacity $trans-default, box-shadow $trans-default;

  &--red {
    background-color: $color-torch-red;
  }

  &--light {
    color: #191919;
    border-color: #191919;
  }

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  // @include hover-focus {
  //   opacity: 0.8;
  // }

  &:hover,
  &:focus {
    opacity: 0.7;
    box-shadow: 1px 5px 10px 5px rgba(black, 30%);
  }
 
  &:active {
    opacity: 0.5;
  }
}
