.main-nav {
  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin: 0;
    margin-right: 20px;
    margin-left: 60px;
    padding: 0;

    list-style: none;
  }

  &__item {
    margin-right: 20px;
  }

  &__link {
    color: $color-default-white;
    transition: color $trans-default;

    &.is-active {
      opacity: 0.6;
    }

    &:hover,
    &:focus {
      color: #a1821a;
    }

    &:active {
      color: rgba(#a1821a, 70%);
    }
  }

  &__toggle {
    display: none;
  }
}
