.header {
  // position: sticky;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 28px 0;
  z-index: 100;

  background-color: #191919;
  background-color: transparent;
  // box-shadow: 1px -1px 12px 2px rgba(255,255,255,10%);

  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    display: flex;
    color: $color-default-white;
    font-size: 48px;
    line-height: 1.3;
  }

  &__callback {
    display: flex;
    flex-direction: column;
    align-items: end;
  }

  &__phone {
    color: $color-default-white;
    font-size: 36px;
    line-height: 1.2;
    transition: color $trans-default;
    padding: 10px;

    &:hover,
    &:focus {
      color: #edb733;
    }

    &:active {
      color: rgba(#edb733, 70%);
    }
  }

  &__callback-btn {
    width: 100%;
    text-transform: uppercase;
    text-wrap: nowrap;
    padding: 12px 19px;
    border-radius: 12px;
    background-color: transparent;
    cursor: pointer;

    & svg {
      width: 24px;
      height: 24px;
      fill: #edb733;
      margin-right: 10px;
    }
  }
}
