.car-covers {
  background-color: #191919;
  padding-top: 100px;
  padding-bottom: 100px;
  min-height: 100vh;
  color: white;

  &--color {
    background-color: #edb733;
    color: #191919
  }

  &__box {
    margin-top: 50px;
    margin-bottom: 50px;
    display: flex;
    gap: 20px;
  }

  &__card {
    background-color: goldenrod;
    padding: 20px;
    width: 50%;
    border-radius: 20px;
    box-shadow: 1px 1px 1px 1px rgba(white, 50%);

      &--color {
        background-color: #191919;
        color: white;
      }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
  }

  &__slide {
    
    & img {
      width: 100%;
      height: 300px;
      object-fit: cover;
      border-radius: 20px;
      transition: transform $trans-600;
      
      &:hover {
        transform: scale(1.2);
      }
    }
  }

  &__description {
    background-color: wheat;
    color: #191919;
    margin: 0;
    margin-bottom: 30px;
    padding: 30px;
    border-radius: 20px;
  }

  &__important {
    display: flex;
    justify-content: flex-end;
    
    & span {
      background-color: #f5f5f5;
      color: #191919;
      margin: 0;
      padding: 10px 20px;
      border-top-left-radius: 12px;
      border-bottom-right-radius: 12px;
    }
  }

   &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    counter-reset: car-covers-advantages;

    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    gap: 30px;
    
  }

  &__item {
    position: relative;
    font-size: 24px;
    line-height: 1.2;

    min-width: 200px;
    box-shadow: 5px 2px 5px 0px rgba(white, 50%);
    padding: 10px;
    padding-right: 30px;
    border-radius: 12px;

    &::before {
      // position: absolute;
      // top: 0;
      // left: 0;
      counter-increment: car-covers-advantages;
      content: counter(car-covers-advantages) " ";
      color: rgba(white, 50%);
      font-size: 60px;
      line-height: 1.5;
    }
  }


  &__box-list {
    width: 100%;
    display: flex;
    gap: 20px;
    justify-content: space-evenly;
    margin-bottom: 30px;
  }

  &__box-item {
    width: 300px;
    background: #191919;
    color: white;
    border-radius: 12px;
    padding: 20px;
    transition: box-shadow $trans-default, transform $trans-default;

    &:hover {
      box-shadow: 1px 3px 20px 5px rgba(white, 80%);
      transform: translateY(-10px);
    }
  }

  &__box-item-price {
    margin: 0;
  }

  &__btn {
    text-align: center;
  }

  &__callback {
    display: flex;
    justify-content: center;
  }
}