.accessories {
  min-height: 100vh;
  background-color: #191919;
  padding-top: 100px;
  padding-bottom: 100px;
  color: white;

  &__list {
    display: flex;
    gap: 20px;
    justify-content: space-evenly;
    list-style: none;
    margin: 0;
    padding: 0;
    margin-bottom: 30px;
    color: #191919;
  }

  &__item {
    max-width: 300px;
    width: 100%;
    background: #ffd500;
    border-radius: 12px;
    padding: 20px;
    transition: box-shadow $trans-default, transform $trans-default;

    &:hover {
      box-shadow: 1px 3px 20px 5px rgba(white, 10%);
      transform: translateY(-10px);
    }

    & strong {
      display: inline-flex;
      margin-bottom: 10px;
    }
  }

  &__item-text {
    margin: 0;
    margin-bottom: 10px;
  }

  &__img {
    border: 10px solid #f5f5f5;
    background-color: #f5f5f5;
    border-radius: 12px;
    transition: transform $trans-default;

    &:hover {
      transform: scale(1.03);
    }

    & img {
      height: 300px;
      width: 100%;
      // height: 500px;
      object-fit: cover;
      border-radius: 12px;
    }
  }

  & .swiper {
    & .accessories__pagination {
      bottom: -5px;
    }
  }
}