.pay-type {
  &__title {
    min-height: 50px;
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    

    &:last-child {
      margin-bottom: 0;
    }

    &::before {
      content: '';
    
      margin-right: 10px;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: wheat;
      align-content: center;
      justify-content: center;
    }
  }

}